import React from "react";

function ThreeMonthAvgCalc(props) {
  const checksForAverage = props.checksForAverage;
  const getAverage = () => {
    let payCheckSum = 0;
    let payCheckAverage = 0;

    let calc = props.grossPayChecks.map((check, index) => {
      if (index >= props.checksForAverage[props.payFreq]) return;
      else {
        payCheckSum += parseFloat(check);
        return (
          <span align="right">
            {parseFloat(check).toFixed(2)} +<br />
          </span>
        );
      }
    });

    const checksPerYear = {
      weekly: 52,
      biWeekly: 36,
      monthly: 12,
      semiMonthly: 24,
      yearly: 1
    };

    console.log(payCheckSum);
    payCheckAverage =
      (payCheckSum / checksForAverage[props.payFreq]) *
      checksPerYear[props.payFreq];

    return (
      <div
        style={{
          border: "1px solid black",
          margin: "0 30%",
          padding: "10px",
          background: "#fffde7"
        }}
      >
        {calc}
        <p>
          {payCheckSum.toFixed(2)} *
          <br />
        </p>
        <span>
          {checksForAverage[props.payFreq]} &divide;
          <br />
        </span>
        <p>{payCheckAverage.toFixed(2)} =</p>
      </div>
    );
  };

  return (
    <div align="center">
      <h3>3 Month Average</h3>
      <h4>
        There are {checksForAverage[props.payFreq]} checks needed for the
        average.{" "}
      </h4>
      <div align="right">{getAverage()}</div>
    </div>
  );
}

export default ThreeMonthAvgCalc;
